//colors
$color_silver_chalice_approx: #aaa;
$white: white;
$color_alto_approx: #ddd;
$wild_sand: #f4f4f4;
$color_boulder_approx: #777;
$color_mine_shaft_approx: #333;

//urls
$url_0: url(data:image/png;base64,);
$url_1: url(data:image/png;base64,);


%extend_1 {
    overflow: hidden;
    display: inline-block;
    width: 1px;
    height: 1px;
    line-height: 300px;
    background: $url_0 no-repeat;
}


@charset 'utf-8';
.tui-timepicker {
    position: relative;
    top: -1px;
    padding: 30px 20px;
    font-weight: bold;
    border: 1px solid $color_silver_chalice_approx;
    background: $white;
    text-align: center;
    * {
            box-sizing: border-box;
    }
    button {
        
        border-radius: 0;
    }
    input {
        font-weight: normal;
        font-size: 16px;
        text-align: center;
        font-weight: normal;
    }
    select {
        font-weight: normal;
    }
    .tui-timepicker-select {
        appearance: none;
        //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
        border-radius: 0;
    }
    .tui-timepicker-select::-ms-expand {
        display: none;
    }
}
.tui-ico-t-btn {
    @extend %extend_1;
}
.tui-timepicker-input-radio {
    @extend %extend_1;
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 16px;
    height: 16px;
    vertical-align: middle;
    background-position: -31px 0;
}
.tui-ico-colon {
    @extend %extend_1;
    width: 2px;
    height: 7px;
    background-position: -17px -28px;
}
.tui-ico-time {
    @extend %extend_1;
    width: 12px;
    height: 12px;
    background-position: 0 -30px;
}
.tui-timepicker-row {
    width: 100%;
    font-size: 0;
}
.tui-timepicker-column {
    display: inline-block;
    vertical-align: middle;
}
.tui-timepicker-btn-area {
    position: relative;
    height: 88px;
    padding: 19px 0;
    .tui-timepicker-spinbox-input {
        width: 100%;
        height: 100%;
        line-height: 46px;
        border: 1px solid $color_alto_approx;
    }
}
.tui-timepicker-spinbox {
    width: 52px;
}
.tui-timepicker-btn {
    position: absolute;
    left: 0;
    width: 100%;
    height: 20px;
    background-color: transparent;
    border: 1px solid $color_alto_approx;
    cursor: pointer;
    &:hover {
        background-color: $wild_sand;
    }
    &:focus {
        background-color: $wild_sand;
    }
    &:active {
        background-color: $wild_sand;
    }
    .tui-ico-t-btn {
        width: 13px;
        height: 7px;
    }
}
.tui-timepicker-btn-up {
    top: 0;
    .tui-ico-t-btn {
        background-position: 0 -12px;
    }
}
.tui-timepicker-btn-down {
    bottom: 0;
    .tui-ico-t-btn {
        background-position: 0 -21px;
    }
}
.tui-timepicker-colon {
    width: 22px;
}
.tui-timepicker-select {
    width: 52px;
    height: 28px;
    padding: 5px 0 5px 9px;
    font-size: 12px;
    border: 1px solid $color_alto_approx;
    background: $url_1 no-repeat;
    background-position: 100% 50%;
    cursor: pointer;
}
.tui-timepicker-check-lst {
    list-style: none;
    padding: 0;
    margin: 0;
}
.tui-timepicker-check {
    margin-top: 11px;
    &:first-child {
        margin-top: 0;
    }
}
.tui-timepicker-checkbox {
    padding-left: 16px;
}
.tui-timepicker-radio {
    overflow: hidden;
    position: relative;
    text-align: left;
    input {
        position: absolute;
        left: -9999px;
        width: 1px;
        height: 1px;
        &:disabled + .tui-timepicker-radio-label .tui-timepicker-input-radio {
            background-position: -31px -36px;
        }
    }
    .tui-timepicker-meridiem-checked + .tui-timepicker-radio-label .tui-timepicker-input-radio {
        background-position: -31px -18px;
    }
}
.tui-timepicker-radio-label {
    display: inline-block;
    padding-left: 20px;
    font-size: 12px;
    line-height: 16px;
    vertical-align: top;
    color: $color_boulder_approx;
    cursor: pointer;
}
.tui-timepicker-area {
    position: relative;
}
.tui-time-input {
    position: relative;
    display: inline-block;
    width: 120px;
    height: 28px;
    border: 1px solid $color_alto_approx;
    input {
        width: 100%;
        height: 100%;
        padding: 0 27px 0 10px;
        font-size: 12px;
        border: 0;
        color: $color_mine_shaft_approx;
        //Instead of the line below you could use @include box-sizing($bs)
        box-sizing: border-box;
    }
    .tui-ico-time {
        position: absolute;
        top: 50%;
        right: 8px;
        margin: -6px 0 0;
        background-position: 0 -30px;
    }
    &.tui-has-focus {
        border-color: $color_silver_chalice_approx;
        .tui-ico-time {
            background-position: 0 -44px;
        }
    }
}
.tui-has-left {
    &.tui-timepicker-body {
        position: relative;
    }
    .tui-timepicker-row {
        position: relative;
        &:after {
            display: block;
            clear: both;
            content: '';
        }
    }
    .tui-is-add-picker {
        float: left;
        padding: 0 5px 0 0;
    }
    .tui-timepicker-checkbox {
        float: left;
        margin-top: 23px;
        padding: 0 16px 0 0;
    }
}
.tui-hidden {
    display: none;
}
.tui-calendar-select-content .tui-timepicker {
    border: 0;
    margin: 0 auto;
}
.tui-timepicker-selectbox + .tui-timepicker-selectbox {
    padding-left: 5px;
}
.tui-timepicker-body .tui-timepicker-colon {
    width: 18px;
}
.tui-timepicker-footer .tui-timepicker-colon {
    width: 18px;
}